.container {
    padding-top: 64px;
}
.page_1_container {
    height: calc(100vh - 80px - 64px);
    padding-left: 45px;
    padding-top: 80px;
}

.page_2_container {
    height: calc(120vh - 80px);
    clear: both;
    background-color: #F8F8F8;
    padding-top: 80px;
}


#student_img_container {
    float: right;
    width: 50%;
    margin-top: -5vh;
}

#student_img_container > img {
    width: 100%;
    height: auto;
}

