}

h1 {
    font-size: 32px;
    font-family: 'Inter', sans-serif;
}

.first_header {
    width: 65vw;
}
.second_header {
    margin-top: 15vh;
    margin-bottom: 5vh;
    width: 40vw;
}

.button:first-of-type {
    margin-right: 1vw;
}

.button {
    
}

.button_container {
    display: flex;
    justify-content: center;
    width: 35vw;
}

.page_2_header_container {
    width: 45vw;
    text-align: center;
    margin-left: 27.5vw;
}

.page_2_header {
    
    
}

.info_header {
    color: #DB0202;
    font-size: 28px;
    margin-bottom: 5px;
    white-space: nowrap;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
}

.info:first-of-type {
    margin-right: 10vw;
}

h3 {
    font-size: 18px;
    color: #2D3436;
    font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Inter', sans-serif;
}

.list_text {
